.container {
  /* scroll-snap-align: center; */
  margin: 5.25vh auto;
}

.page {
  opacity: 0;
  margin: 25px 50px;
  background-size: 130% 130%;
  background-position: center;
  background-repeat: no-repeat;
  /* background-size: cover; */
  height: 850px;
  width: 600px;
  border: 2px solid black;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 1.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.sacredGeo {
  filter: drop-shadow(0px 0px .1px black);
  transform: rotate(-5deg);
  color: #000;
  z-index: -1;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  width: 200px;
  height: 200px;
  background-image: url(./imgs/01.png);
}

.title {
  padding: 5% 2.25%;
  height: 12%;
  font-family: 'IM Fell English SC', serif;
  font-weight: 900;
}

.challenge p {
  margin: 0 auto;
  padding: .5rem 0;
  font-family: 'IM Fell English SC', serif;
  font-size: 1.1rem;
  font-weight: 700;
  color: black;
  text-align: center;
}

h2 {
  font-family: 'IM Fell English SC', serif;
  margin: 0 auto;
  font-size: 2.5rem;
  font-weight: 700;
  color: black;
  text-align: center;
}

h3 {
  font-family: 'IM Fell English SC', serif;
  margin: 0 auto;
  padding: 0;
  font-size: 1.5rem;
  width: fit-content;
  color: black;
  text-align: center;
  border-bottom: 3px solid black;
}

.pictureContainer {
  position: relative;
  margin: 0 auto;
  padding: 0;
  height: 35%;
  width: 80%;
  border-radius: 5px;
  border: 5px solid black;
  background-color: #2D3237;
  /* object-fit: contain; */
}

.picture {
  object-fit: contain;
  position: absolute;
  /* margin: 0 auto;
  padding: 0; */
  height: 100%;
  width: 100%;
  left: 0; 
  right: 0;
  bottom: 0; 
  /* background-color: #2D3237; */
  /* background-repeat: no-repeat; */
  /* background-size: contain; */
  /* object-fit: contain; */
  /* background-position: center; */
  /* border-radius: 5px;
  border: 1px solid black; */
  /* padding-top: 44.42%; */
}

.savingThrows {
  position: absolute;
  padding: auto;
  margin: 0 auto;
  left: 0; 
  right: 0;
  bottom: 0; 

  /* padding: 2% 0 0; */
  /* margin: 0 auto -2%; */
  /* border: 2px solid black; */
  border-radius: 1px;
  background: rgb(0,0,0);
  background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(45,50,55,1) 50%, rgba(0,0,0,1) 100%);
  height: 15%;
  width: auto;
}

.savingThrows p {
  text-align: center;
  margin: 2.5% auto;
  color: whitesmoke;
  font-family: 'IM Fell English SC', serif;
  font-size: 1.05rem;
  font-weight: 700;
}

.details {
  border: 2px solid black;
  border-radius: 5px;
  margin: 1.5% auto 0;
  padding: 0;
  display: flex;
  background: radial-gradient(ellipse farthest-corner at right bottom, #FEDB37 0%, #FDB931 8%, #9f7928 30%, #8A6E2F 40%, transparent 80%),
  radial-gradient(ellipse farthest-corner at left top, #FFFFFF 0%, #FFFFAC 8%, #D1B464 25%, #5d4a1f 62.5%, #5d4a1f 100%);
  width: 80%;
}

.details :nth-child(1) {
  background: rgb(0,0,0);
  background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(85,126,240,1) 10%, rgba(90,111,255,1) 50%, rgba(85,126,240,1) 90%, rgba(0,0,0,1) 100%);
}

.details :nth-child(2) {
  background: rgb(0,0,0);
  background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(240,85,85,1) 10%, rgba(255,90,90,1) 50%, rgba(240,85,85,1) 90%, rgba(0,0,0,1) 100%);
}

.details :nth-child(3) {
  background: rgb(0,0,0);
  background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgb(88, 240, 85) 10%, rgb(98, 255, 90) 50%, rgb(88, 240, 85) 90%, rgba(0,0,0,1) 100%);
}

.details p {
  border: 2px solid #2D3237;
  border-radius: 5px;
  margin: 1%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-family: 'IM Fell English SC', serif;
  font-size: 1.2rem;
  font-weight: 900;
  border: 2px solid black;
  border-radius: 5px;
  line-height: 100%;
  width: 33%;
  height: 65px;
}

.content {
  margin: 0 auto;
  display: flex;
  border-radius: 10px;
  height: 30%;
  width: 80%;
}

.stats {
  border: 2px solid black;
  border-radius: 5px;
  background-color: #2D3237;

  background: radial-gradient(ellipse farthest-corner at right bottom, #efefef 0%, #d7d7d7 8%, #919191 30%, #747474 40%, transparent 80%),
  radial-gradient(ellipse farthest-corner at left top, #d5d5d5 0%, #bbbbbb 8%, #9c9c9c 25%, #565656 62.5%, #3c3c3c 100%);
  line-height: 30%;
  margin: 2.5%;
  padding: 1%;
  width: 25%;
  padding: 1rem;
}

.stats ul {
  
  list-style-type: hebrew;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 0 0 -25%;
    font-family: 'IM Fell English SC', serif;
    font-size: auto;
    font-weight: 700;
}


.stats li {
  height: 1.75em;
  width: 6.75em;
  border: 2px solid #2D3237;
  border-radius: 5px;
  background: url(./imgs/papyrus.png);
}

.body {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
  border: 2px solid #2D3237;
  border-radius: 5px;
  background: url(./imgs/papyrus.png);
  background-repeat: repeat-y;
  background-attachment: local;
  margin: 2.5%;
  padding: 5%;
  width: 65%;
  height: auto;
  overflow-y:auto;
}

.body ul {
  text-align: left;
  margin: auto 2%;
  padding: 1%;
  font-family: 'IM Fell English SC', serif;
  font-size: 1rem;
  font-weight: 900;
  line-height: 150%;
}





.grow { 
  transition: all .1s ease-in-out; 
}
.grow:hover {
  transform: scale(1.05) translateY(-5px);
  filter: drop-shadow(5px 5px 10px #000);
}
.fade-bottom {
  animation: fade-bottom .25s cubic-bezier(1,-0.03,0,1.01);
  animation-fill-mode: forwards;
  /* animation-delay: 0s; */
}

@keyframes fade-bottom {
  0% {
    opacity: 0;
    transform: scale(.5)translateY(900px);
  }
  10% {
    opacity: .5;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}